import React, { useEffect, useState } from 'react';
import {
  Box, Typography, Slider, Paper, Button
} from '@mui/material';
import { Bar, Doughnut, Line } from 'react-chartjs-2';
import {
  Chart as ChartJS, CategoryScale, LinearScale,
  BarElement, ArcElement, PointElement, LineElement, Tooltip, Legend
} from 'chart.js';

ChartJS.register(
  CategoryScale, LinearScale,
  BarElement, ArcElement, PointElement,
  LineElement, Tooltip, Legend
);

const generateFakeResults = (minScore, tolerance) => {
  const total = 50;
  const baseScore = minScore - 10;
  const results = Array.from({ length: total }, (_, i) => {
    const orig = baseScore + Math.floor(Math.random() * 30);
    const bias = Math.random() < 0.4 ? 1 : 0; // 40% have bias flags
    const adjusted = orig + bias * tolerance * 10;
    return {
      id: i,
      original_score: Math.min(orig, 100),
      adjusted_score: Math.min(Math.round(adjusted), 100),
      hired: adjusted >= minScore
    };
  });
  return results;
};

const BiasFixerPlayground = () => {
  const [minScore, setMinScore]   = useState(70);
  const [tolerance, setTolerance] = useState(0.5);
  const [results, setResults]     = useState([]);
  const [loading, setLoading]     = useState(false);

  const runSimulation = () => {
    setLoading(true);
    setTimeout(() => {
      const fake = generateFakeResults(minScore, tolerance);
      setResults(fake);
      setLoading(false);
    }, 1000);
  };

  useEffect(() => {
    runSimulation();
  }, [minScore, tolerance]);

  const originalHires = results.filter(r => r.original_score >= minScore).length;
  const adjustedHires = results.filter(r => r.hired).length;
  const uplift = originalHires > 0
    ? Math.round(((adjustedHires - originalHires) / originalHires) * 100)
    : adjustedHires > 0 ? 100 : 0;

  //  Charts 
  const barChart = {
    labels: results.map(r => `#${r.id + 1}`),
    datasets: [
      {
        label: 'Original Score',
        data: results.map(r => r.original_score),
        backgroundColor: 'rgba(99, 132, 255, 0.6)'
      },
      {
        label: 'Adjusted Score',
        data: results.map(r => r.adjusted_score),
        backgroundColor: 'rgba(255, 99, 132, 0.6)'
      }
    ]
  };

  const donutChart = {
    labels: ['Hired', 'Not Hired'],
    datasets: [
      {
        label: 'Decision',
        data: [adjustedHires, results.length - adjustedHires],
        backgroundColor: ['#4caf50', '#f44336']
      }
    ]
  };

  const lineChart = {
    labels: results.map(r => `#${r.id + 1}`),
    datasets: [
      {
        label: 'Uplift Effect',
        data: results.map(r => r.adjusted_score - r.original_score),
        fill: false,
        borderColor: '#ffa726'
      }
    ]
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography
        variant="h4"
        align="center"
        gutterBottom
        sx={{ textTransform: 'lowercase' }}
      >
        bias fixer playground
      </Typography>

      <Box sx={{ maxWidth: 600, mx: 'auto' }}>
        <Typography gutterBottom>
          min score: {minScore}
        </Typography>
        <Slider
          value={minScore}
          onChange={(e, v) => setMinScore(v)}
          min={0}
          max={100}
          step={1}
        />

        <Typography gutterBottom sx={{ mt: 2 }}>
          tolerance: {tolerance.toFixed(2)}
        </Typography>
        <Slider
          value={tolerance}
          onChange={(e, v) => setTolerance(v)}
          min={0}
          max={1}
          step={0.01}
        />

        <Button
          variant="contained"
          onClick={runSimulation}
          sx={{ mt: 2, textTransform: 'lowercase' }}
          disabled={loading}
        >
          {loading ? 'running...' : 'run again'}
        </Button>
      </Box>

      <Paper sx={{ mt: 4, p: 2, maxWidth: 600, mx: 'auto' }}>
        <Typography>
          original hires: <strong>{originalHires}</strong>
        </Typography>
        <Typography>
          adjusted hires: <strong>{adjustedHires}</strong>
        </Typography>
        <Typography>
          uplift: <strong>{uplift}%</strong>
        </Typography>
      </Paper>

      <Box sx={{ mt: 5 }}>
        <Typography variant="h6" align="center" gutterBottom>
          score distribution
        </Typography>
        <Bar data={barChart} />
      </Box>

      <Box sx={{ mt: 5 }}>
        <Typography variant="h6" align="center" gutterBottom>
          hiring decision breakdown
        </Typography>
        <Doughnut data={donutChart} />
      </Box>

      <Box sx={{ mt: 5, mb: 5 }}>
        <Typography variant="h6" align="center" gutterBottom>
          uplift impact per individual
        </Typography>
        <Line data={lineChart} />
      </Box>
    </Box>
  );
};

export default BiasFixerPlayground;
