// src/components/SignUpPage.js
import React, { useState } from 'react';
import {
  Button, TextField, Typography, Paper, MenuItem
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const SignUpPage = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [role, setRole]         = useState('user');
  const [error, setError]       = useState('');
  const [success, setSuccess]   = useState('');
  const [loading, setLoading]   = useState(false);

  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    setLoading(true);

    try {
      const { data } = await axios.post('/api/signup', {
        username: username.trim().toLowerCase(),
        password,
        role
      });

      if (data.status === 'signup success') {
        setSuccess('account created! redirecting');
        setTimeout(() => navigate('/login'), 1500);
      } else {
        setError('signup failed, try again');
      }
    } catch (err) {
      console.error(err);
      if (err.response?.status === 409) {
        setError('username already taken');
      } else {
        setError(err.response?.data?.error || 'an error occurred during signup');
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <Paper
      sx={{
        maxWidth: 400,
        margin: 'auto',
        mt: '8rem',
        p: 3,
        backgroundColor: '#f4f6f8',
        boxShadow: 3,
        fontFamily: "'Roboto Mono', monospace"
      }}
    >
      <Typography
        variant="h5"
        align="center"
        sx={{ mb: 2, color: '#333', textTransform: 'lowercase' }}
      >
        sign up
      </Typography>

      {error && (
        <Typography variant="body2" align="center" color="error" sx={{ mb: 2 }}>
          {error}
        </Typography>
      )}
      {success && (
        <Typography variant="body2" align="center" color="primary" sx={{ mb: 2 }}>
          {success}
        </Typography>
      )}

      <form onSubmit={handleSubmit}>
        <TextField
          fullWidth
          label="username"
          variant="outlined"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          placeholder="create a username"
          sx={{ mb: 2, textTransform: 'lowercase' }}
          InputProps={{
            style: { fontFamily: "'Roboto Mono', monospace", textTransform: 'lowercase' }
          }}
        />

        <TextField
          fullWidth
          label="password"
          variant="outlined"
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          placeholder="create a password"
          sx={{ mb: 2 }}
          InputProps={{ style: { fontFamily: "'Roboto Mono', monospace" } }}
        />

        <TextField
          select
          fullWidth
          label="role"
          value={role}
          onChange={(e) => setRole(e.target.value)}
          sx={{ mb: 3 }}
          InputProps={{ style: { fontFamily: "'Roboto Mono', monospace" } }}
        >
          <MenuItem value="user">user</MenuItem>
          <MenuItem value="admin">admin</MenuItem>
        </TextField>

        <Button
          type="submit"
          variant="contained"
          fullWidth
          disabled={loading}
          sx={{
            backgroundColor: '#ff5722',
            color: '#fff',
            fontWeight: 600,
            borderRadius: '30px',
            py: 1.5,
            textTransform: 'lowercase',
            fontFamily: "'Roboto Mono', monospace",
            '&:hover': { backgroundColor: '#e64a19' }
          }}
        >
          {loading ? 'creating' : 'sign up'}
        </Button>
      </form>
    </Paper>
  );
};

export default SignUpPage;
