import React, { useState } from 'react';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
  useLocation,
  useNavigate,
  Navigate,
} from 'react-router-dom';
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  Container,
  Tabs,
  Tab,
} from '@mui/material';
import axios from 'axios';

import WelcomePage from './components/WelcomePage';
import LoginPage from './components/LoginPage';
import SignUpPage from './components/SignUpPage';
import InteractiveDashboard from './components/InteractiveDashboard';
import AdminDashboard from './components/AdminDashboard';
import Help from './components/Help';
import SimulatedHiringPage from './components/SimulatedHiringPage';
import BiasFixerPlayground from './components/BiasFixerPlayground';
import ResultsPage from './components/ResultsPage';

// Custom hook to sync tabs with current path
function NavTabs() {
  const location = useLocation();
  return (
    <Tabs
      value={location.pathname}
      textColor="inherit"
      indicatorColor="secondary"
      sx={{
        '& .MuiTab-root': {
          textTransform: 'lowercase',
          fontFamily: 'Roboto Mono',
          minWidth: 100,
        },
      }}
    >
      <Tab label="welcome" value="/" component={Link} to="/" />
      <Tab label="help" value="/help" component={Link} to="/help" />
      <Tab label="simulate" value="/simulate" component={Link} to="/simulate" />
      <Tab label="bias fixer" value="/bias-fixer" component={Link} to="/bias-fixer" />
      <Tab label="results" value="/results" component={Link} to="/results" />
      <Tab label="dashboard" value="/dashboard" component={Link} to="/dashboard" />
    </Tabs>
  );
}

const AppShell = () => {
  const [user, setUser] = useState(null);
  const navigate = useNavigate();

  const handleLogout = async () => {
    try {
      await axios.get('/api/logout');
      setUser(null);
      navigate('/');
    } catch (err) {
      console.error('Logout failed', err);
    }
  };

  return (
    <>
      <AppBar position="static" color="primary" elevation={4} sx={{ mb: 2 }}>
        <Toolbar>
          <Typography
            variant="h6"
            sx={{ flexGrow: 1, textTransform: 'lowercase', fontFamily: 'Roboto Mono' }}
          >
            visualizing bias
          </Typography>
          <NavTabs />
          {user ? (
            <>
              <Typography
                variant="body1"
                sx={{ mx: 2, textTransform: 'lowercase', fontFamily: 'Roboto Mono' }}
              >
                {`logged in as ${user.username}`}
              </Typography>
              <Button
                color="inherit"
                component={Link}
                to="/dashboard"
                sx={{ textTransform: 'lowercase', fontFamily: 'Roboto Mono' }}
              >
                dashboard
              </Button>
              {user.role === 'admin' && (
                <Button
                  color="inherit"
                  component={Link}
                  to="/admin"
                  sx={{ textTransform: 'lowercase', fontFamily: 'Roboto Mono' }}
                >
                  admin dashboard
                </Button>
              )}
              <Button
                color="inherit"
                onClick={handleLogout}
                sx={{ textTransform: 'lowercase', fontFamily: 'Roboto Mono' }}
              >
                logout
              </Button>
            </>
          ) : (
            <>
              <Button
                color="inherit"
                component={Link}
                to="/login"
                sx={{ textTransform: 'lowercase', fontFamily: 'Roboto Mono' }}
              >
                login
              </Button>
              <Button
                color="inherit"
                component={Link}
                to="/signup"
                sx={{ textTransform: 'lowercase', fontFamily: 'Roboto Mono' }}
              >
                sign up
              </Button>
            </>
          )}
        </Toolbar>
      </AppBar>

      <Container sx={{ mt: 3, fontFamily: 'Roboto Mono' }}>
        <Routes>
          <Route path="/" element={<WelcomePage />} />
          <Route path="/login" element={<LoginPage onLogin={setUser} />} />
          <Route path="/signup" element={<SignUpPage />} />
          <Route path="/dashboard" element={<InteractiveDashboard />} />
          <Route
            path="/admin"
            element={
              user ? (
                user.role === 'admin' ? (
                  <AdminDashboard />
                ) : (
                  <Typography variant="h6" sx={{ mt: 4, textAlign: 'center' }}>
                    unauthorized access  admin only
                  </Typography>
                )
              ) : (
                <Navigate to="/login" replace />
              )
            }
          />
          <Route path="/help" element={<Help />} />
          <Route path="/simulate" element={<SimulatedHiringPage />} />
          <Route path="/bias-fixer" element={<BiasFixerPlayground />} />
          <Route path="/results" element={<ResultsPage />} />
        </Routes>
      </Container>
    </>
  );
};

const App = () => (
  <Router>
    <AppShell />
  </Router>
);

export default App;
