import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  TextField,
  MenuItem,
  Button,
  Alert
} from '@mui/material';
import axios from 'axios';

const SimulatedHiringPage = () => {
  const [countries, setCountries] = useState([]);
  const [formData, setFormData]   = useState({
    country: '',
    age_group: 0,
    education_level: 1,
    professional_developer: 1,
    years_code:  0
  });
  const [result, setResult] = useState(null);

  useEffect(() => {
    axios.get('/api/countries')
      .then(r => setCountries(r.data))
      .catch(e => console.error('Failed to load countries', e));
  }, []);

  const handleChange = field => e =>
    setFormData({ ...formData, [field]: e.target.value });

  const handleSubmit = async () => {
    if (!formData.country) {
      return alert('Please select a country before running the simulation.');
    }

    const countryKey = formData.country.toLowerCase();
    const stats = await axios.get(`/api/country-stats/${countryKey}`)
      .then(res => res.data)
      .catch(() => null);

    if (!stats) {
      return alert(`Missing stats for "${formData.country}". Try another.`);
    }

    const payload = {
      age_group:              Number(formData.age_group),
      education_level:        Number(formData.education_level),
      professional_developer: Number(formData.professional_developer),
      years_code:             Number(formData.years_code),
      pct_female_highered:    stats.Pct_Female_HigherEd,
      pct_male_highered:      stats.Pct_Male_HigherEd,
      pct_female_mided:       stats.Pct_Female_MidEd,
      pct_male_mided:         stats.Pct_Male_MidEd,
      pct_female_lowed:       stats.Pct_Female_LowEd,
      pct_male_lowed:         stats.Pct_Male_LowEd
    };

    if (Object.values(payload).some(v => v === undefined || v === null)) {
      return alert('Some values in the payload are missing or invalid.');
    }

    try {
      const { data: mlResult } = await axios.post('/api/predict', payload);
      setResult(mlResult);
    } catch (err) {
      console.error('Simulation error', err);
      setResult({
        error: err.response?.data?.error || err.message || 'Something went wrong'
      });
    }
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography
        variant="h4"
        align="center"
        gutterBottom
        sx={{ textTransform: 'lowercase' }}
      >
        simulate hiring outcome
      </Typography>

      <Box sx={{ maxWidth: 500, mx: 'auto' }}>
        <TextField
          select
          label="country"
          value={formData.country}
          onChange={handleChange('country')}
          fullWidth
          margin="normal"
        >
          {countries.map(c => (
            <MenuItem key={c} value={c}>{c}</MenuItem>
          ))}
        </TextField>

        <TextField
          label="age group"
          type="number"
          helperText="0 = &lt;35, 1 = 35"
          value={formData.age_group}
          onChange={handleChange('age_group')}
          fullWidth
          margin="normal"
        />

        <TextField
          label="education level"
          type="number"
          helperText="0 = HS, 1 = Bach, 2 = Master, 3 = PhD"
          value={formData.education_level}
          onChange={handleChange('education_level')}
          fullWidth
          margin="normal"
        />

        <TextField
          label="professional developer"
          type="number"
          helperText="0 = No, 1 = Yes"
          value={formData.professional_developer}
          onChange={handleChange('professional_developer')}
          fullWidth
          margin="normal"
        />

        <TextField
          label="years coding"
          type="number"
          helperText="Total years of experience coding"
          value={formData.years_code}
          onChange={handleChange('years_code')}
          fullWidth
          margin="normal"
        />

        <Button
          variant="contained"
          onClick={handleSubmit}
          sx={{ mt: 2, textTransform: 'lowercase' }}
        >
          run simulation
        </Button>
      </Box>

      {result && (
        <Box sx={{ mt: 4 }}>
          {result.error ? (
            <Alert severity="error">{result.error}</Alert>
          ) : (
            <Alert severity="success">
              qualification_score: <strong>{result.qualification_score}</strong><br />
              ai_decision: <strong>{result.ai_decision}</strong>
            </Alert>
          )}
        </Box>
      )}
    </Box>
  );
};

export default SimulatedHiringPage;
